@import './assets/css/normalize.css';
@import url('https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap');

.domain {
  width: 90%;
  max-width: 700px;
  margin: 0 auto;

  &-header {
    padding: 0 0 24px 0;
    h1 {
      text-align: center;
      font-size: 32px;
      line-height: 1.5;
      color: #202124;
      padding: 60px 0 32px;
    }
    p {
      line-height: 1.6;
      color: #414245;
      a {
        color: #ff6464;
      }
    }
  }

  &-form {
    label {
      line-height: 1.6;
      color: #414245;
      display: block;
      padding: 0 0 16px 0;
    }
    input {
      width: 84.5%;
      height: 52px;
      float: left;
      border-radius: 4px;
      border: solid 1px #bcbdc1;
      background-color: #ffffff;
      font-size: 16px;
      line-height: 52px;
      padding: 0 15px;
      box-sizing: border-box;
      outline: 0;
      &:focus {
        border-color: #414245;
      }
      &.error {
        border-color: #d83a15;
      }
    }
    button {
      float: right;
      width: 14%;
      height: 52px;
      border-radius: 4px;
      background-color: #ff6464;
      color: #fff;
    }
    .msg-err {
      clear: both;
      padding: 8px 0;
      color: #d83a15;
      font-size: 12px;
    }
  }

  &-content {
    padding: 40px 0;
    .default {
      img {
        width: 100%;
      }
    }
    .box {
      margin-bottom: 10px;
      header {
        height: 52px;
        line-height: 52px;
        overflow: hidden;
        background: #f4f5f9 url('./assets/images/v@2x.png') 80px 50% no-repeat;
        background-size: 24px 24px;
        h1 {
          height: 52px;
          width: 95px;
          line-height: 52px;
          font-size: 18px;
          color: #202124;
          padding: 0 0 0 20px;
          display: inline-block;
          float: left;
          text-transform: uppercase;
        }
        small {
          display: inline-block;
          float: left;
          font-size: 16px;
          color: #414245;
        }
        &.warn {
          background-image: url('./assets/images/warn@2x.png');
        }
      }
      .text {
        border: solid 1px #edeef2;
        box-sizing: border-box;
        padding: 25px 20px;

        line-height: 1.6;
        strong {
          font-weight: bold;
        }
        a {
          color: #ff6464;
        }
        pre {
          font-size: 14px;
          font-family: 'Roboto Mono', monospace;
          background-color: #f4f5f9;
          border: 0;
          margin: 5px 0 0 0;
          padding: 7px 10px;
          white-space: pre-line;
        }
        .msg {
          margin-top: 20px;
        }
      }
    }
  }
  &-guide {
    line-height: 1.6;
    color: #414245;
    padding: 30px 0;
    p {
      padding-bottom: 10px;
      em {
        color: #ff6464;
      }
    }
    ul {
      padding-bottom: 10px;
      margin: 0 0 0 30px;
      li {
        list-style-type: disc;
      }
    }
    a {
      color: #ff6464;
    }
  }
}

.divider {
  width: 90%;
  max-width: 700px;
  margin: 0 auto;

  &-header {
    padding: 0 0 24px 0;
    h1 {
      text-align: center;
      font-size: 32px;
      line-height: 1.5;
      color: #202124;
      padding: 60px 0 32px;
    }
    p {
      line-height: 1.6;
      color: #414245;
      a {
        color: #ff6464;
      }
    }
  }

  .generator {
    text-align: center;
    border: 1px solid #dcdce1;
    overflow: hidden;

    .toolbox {
      display: flex;
      box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);

      .tools {
        display: flex;
        width: calc(100% / 6 * 5);

        .tool {
          width: 100%;
          padding: 5px;
          border-right: 1px solid #dcdce1;
          border-bottom: 2px solid #ffcb08;

          &.selected {
            background: #ffcb08;
          }

          .icon {
            user-select: none;
            width: 24px;
          }
        }
      }

      .download {
        width: calc(100% / 6);
        display: inline-block;
        border: 0;
        padding: 5px;
        border-bottom: 2px solid #ffcb08;

        .icon {
          user-select: none;
          width: 24px;
        }

        &.selected {
          background: #ffcb08;
        }
      }
    }

    .container {
      position: relative;
      display: flex;
      overflow: hidden;

      .grid {
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0.5;
        z-index: -1;
      }

      .canvas,
      .canvas-container,
      .container,
      .grid {
        width: 100% !important;
        height: unset !important;
        aspect-ratio: auto 630 / 300 !important;
      }

      .alt {
      }
    }

    .controller {
      background: #f4f4f4;
      border-top: 2px solid #e6e6e6;

      &-main {
        width: 100%;
        border-right: 1px solid #ddd;
      }
      &-sub {
        width: 100%;
        background: #fafafa;
      }

      &-container {
        display: flex;
        margin: 0 auto;
        .inputbox {
          display: flex;
          justify-content: flex-start;
          height: 34px;
          // background: #fafafa;

          padding: 4px;
          &-label {
            user-select: none;
            word-break: keep-all;
            display: inline-block;
            width: 20%;
            font-size: 12px;
            font-weight: 700;
            padding: 10px 0;
          }

          &.range .inputbox-wrapper {
            display: inline-block;
            background: #fff;
            padding: 0 8px;
            border: 1px solid #e6e6e6;
          }

          &.checkbox .inputbox-wrapper {
            padding: 6px 0;
          }

          &-input {
            &[type='range'] {
              outline: 0;
              width: 100%;
              appearance: none;
              cursor: pointer;
              padding: 0;
              height: 1px;
              border: 0;
              background: #dcdce1;
              margin-top: 16px;

              &::-webkit-slider-thumb {
                appearance: none;
                padding: 0;
                background: #ffcb08;
                height: 10px;
                width: 10px;
              }

              &.reverse {
                direction: rtl;
              }
            }

            &[type='checkbox'] {
              display: none;
            }

            &-checkbox {
              // display: inline-block;
              width: 16px;
              height: 16px;
              background: #fff;
              border: 1px solid #e6e6e6;
              border-radius: 3px;
              cursor: pointer;

              .icon {
                user-select: none;
                background: #ffcb08;
                width: 16px;
              }
            }
          }

          &-sub {
            user-select: none;
            text-align: left;
            white-space: nowrap;
            background: #f9f9f9;
            font-size: 12px;
            line-height: 2;
            padding: 4px;
            padding-right: 8px;
            border: 1px solid #e6e6e6;
            border-left: 0;
            overflow: hidden;

            &-value {
              width: 50px;
              background: none;
              text-align: right;
              vertical-align: baseline;
              padding: 0;
              border: 0;
              outline: 0;
            }

            &-max {
              display: inline-block;
              width: 30px;
            }
          }
        }
      }
    }
  }
}

.site-header {
  border-bottom: 2px solid #f4f4f5;
  &-inner {
    width: 90%;
    max-width: 1020px;
    height: 60px;
    margin: 0 auto;
  }
  .logo {
    display: inline-block;
    a {
      display: block;
      padding: 0 0;
      img {
        width: 100px;
        display: inline-block;
        vertical-align: middle;
      }
      span {
        display: inline-block;
        color: #212124;
        font-size: 20px;
        font-weight: normal;
        line-height: 1;
        // padding: 0 0 0 6px;
      }
    }
  }

  .menu {
    display: inline-block;
    margin-left: 25px;

    &-item {
      display: inline-block;
      padding: 24px 0 20px;
      color: #9d9ea2;
      font-weight: 700;
      margin: 0 15px;

      &:hover,
      &.active {
        color: #202124;
        border-bottom: 2px solid #ff6464;
      }
    }
  }

  .stibee-logo {
    float: right;
    a {
      display: inline-block;
      padding: 15px;
      img {
        width: 16px;
        display: block;
      }
    }
  }
}

@media screen AND (max-width: 640px) {
  .domain {
    &-form {
      input {
        width: 72%;
      }
      button {
        width: 26%;
      }
    }
  }
}
